import pathToRegexp from 'path-to-regexp'

export function parseImgHash(hash) {
  if (typeof hash !== 'string') {
    hash = String(hash)
  }
  if (!hash) return ''
  return ['/', hash.slice(0,1), '/', hash.slice(1,3), '/', hash.slice(3), '.', hash.slice(32)].join('')
}

export function formatImgParams(paramList) {
  if (!Array.isArray(paramList)) {
    throw new TypeError('argument 1 must be array!')
  }
  if (!paramList.length) return ''
  return `?imageMogr/${paramList.reduce((rst, pair) => [...rst, pair.join('/')], []).join('/')}/`
}


export function parseImageMogrParam(str) {
  if (typeof str !== 'string') {
    throw new TypeError('the first argument must be string!')
  }
  if (str[0] !== '/') {
    str = '/' + str
  }
  if (str.slice(-1)[0] !== '/') {
    str += '/'
  }

  const params = str.split('/').slice(1, -1)
  const len = params.length
  let i = 0
  let rst = []
  while (i < len) {
    const key = params[i]
    if (key === 'auto-orient' || key === 'strip') {
      rst.push([key, true])
    } else {
      const value = params[++i]
      rst.push([key, value])
    }
    i++
  }
  return rst
}

